import { Box, Button, Container, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";
import PhoneIcon from "@mui/icons-material/Phone";
import EmailIcon from "@mui/icons-material/Email";
import { useSelector } from "react-redux";

const englishData = {
  title: "Buy and Sell the latest used cars for the fairest prices.",
  txt: "Passenger cars, vans, light trucks and even margin cars or damagedvehicles, youre sure to find what youre looking for.",
  firstBtn: "+393779452678",
  secondBtn: "Contact Us",
};
const italyData = {
  title: "Acquista e vendi le ultime auto usate ai prezzi pi equi.  ",
  txt: "Autovetture, furgoni, autocarri leggeri e persino auto marginali o veicoli danneggiati, troverai sicuramente quello che stai cercando.",
  firstBtn: "+393779452678",
  secondBtn: "chiamaci",
};
const francaData = {
  title:
    "Achetez et vendez les dernires voitures d'occasion aux prix les plus bas.",
  txt: "Voitures de tourisme, fourgonnettes, camions lgers et mme voitures  marge ou vhicules accidents, vous tes sr de trouver ce que vous cherchez.  ",
  firstBtn: "+393779452678",
  secondBtn: "Contactez-nous",
};
const arabicData = {
  title: "      ",
  txt: "                   .",
  firstBtn: "+",
  secondBtn: "   ",
};

const SliderThree = () => {
  const language = useSelector((state) => state.LanguageSlice.language);
  const [CountryData, setCountryData] = useState(italyData);
  useEffect(() => {
    if (language === "italy") {
      return setCountryData(italyData);
    } else if (language === "english") {
      return setCountryData(englishData);
    } else if (language === "franca") {
      return setCountryData(francaData);
    } else if (language === "Arabic") {
      return setCountryData(arabicData);
    }
  }, [language]);

  return (
    <Box
      className="first-banner"
      sx={{
        position: "relative",
        backgroundImage: `url(https://images.ctfassets.net/00neft81gbdp/4tIaUmel3TCreF9utZfm39/ab128f4c5a1dd92557c8f53f4478592f/three-slide.jpg)`,
        height: "100%",
        direction: language === "Arabic" ? "rtl" : "ltr",
      }}
    >
      <Container sx={{ height: "100%" }}>
        <Box
          sx={{
            display: "flex",
            alignItems: "flex-start",
            justifyContent: "center",
            height: "100%",
            flexDirection: "column",
          }}
        >
          <Typography
            variant="h1"
            sx={{
              color: "#fff",
              fontSize: { xs: "2rem", sm: "3rem", md: "5rem" },
              letterSpacing: language === "Arabic" ? "0px" : "2px",
              fontWeight: "bold",
              textAlign: "start",
              width: { xs: "100%", md: "80%" },
            }}
          >
            {CountryData.title}
          </Typography>
          <Typography
            variant="body2"
            sx={{
              fontSize:
                language === "Arabic" ? { xs: "22px", md: "30px" } : "22px",
              fontWeight: "bold",
              letterSpacing: language === "Arabic" ? "0px" : "2px",
              width: { xs: "100%", md: "70%" },
              marginTop: "10px",
              color: "#795548",
            }}
          >
            {CountryData.txt}
          </Typography>
          <Box
            sx={{
              display: "flex",
              justifyContent: {
                xs: "space-between",
                md: language === "Arabic" ? "flex-end" : "flex-start",
              },
              alignItems: "center",
              width: "100%",
              marginTop: "30px",
              direction: "ltr",
            }}
          >
            <Button
              sx={{
                color: "#3e2723",
                backgroundColor: "#fff",
                fontWeight: "bold",
                margin: { xs: 0, md: "0px 20px" },
                ":hover": {
                  color: "#fff",
                },
              }}
              variant="contained"
              startIcon={<PhoneIcon />}
            >
              {CountryData.firstBtn}
            </Button>

            <Button
              sx={{ fontWeight: "bold" }}
              variant="contained"
              startIcon={<EmailIcon />}
            >
              {CountryData.secondBtn}
            </Button>
          </Box>
        </Box>
      </Container>
    </Box>
  );
};

export default SliderThree;
