import { Box, Button, Menu, MenuItem, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";
import PopupState, { bindTrigger, bindMenu } from "material-ui-popup-state";

const CountryControl = ({ getCountryName }) => {
  const data = [
    {
      name: "english",
      flag: "https://images.ctfassets.net/00neft81gbdp/5dbwyjHzqfybvMU4RgGT8l/1e47587cd50ddd1ecfa154ce3393757f/Flag_of_the_United_States.svg.png",
    },
    {
      name: "italy",
      flag: "https://images.ctfassets.net/00neft81gbdp/6C4BfG0IhJS0dDG1OFLjY/4eb7789a6f91e04d520b3b52574a61fe/italy-flag-icon.svg",
    },
    {
      name: "franca",
      flag: "https://images.ctfassets.net/00neft81gbdp/5ep4gajJHG7EwRSzPhdW4s/004be904be957a73d0bf2034ca6c01a3/2560px-Flag_of_France__bordered_.svg.png",
    },
    {
      name: "Arabic",
      flag: "https://images.ctfassets.net/00neft81gbdp/5k0rRHj6gSZe7E0IQG4kLw/1b20038d93412ee8a29a57b96f58cf49/Flag_of_the_Arab_League.svg.png",
    },
  ];

  const [selectCountry, SetselectCountry] = useState(null);
  if (selectCountry == null) {
    SetselectCountry({
      name: "itely",
      countryFlag:
        "https://images.ctfassets.net/00neft81gbdp/6C4BfG0IhJS0dDG1OFLjY/4eb7789a6f91e04d520b3b52574a61fe/italy-flag-icon.svg",
    });
  }
  useEffect(() => {
    getCountryName(selectCountry.name);
  }, [selectCountry]);
  return (
    <PopupState variant="popover" popupId="demo-popup-menu">
      {(popupState) => (
        <React.Fragment>
          <Box>
            <Button
              color="inherit"
              variant="text"
              {...bindTrigger(popupState)}
              sx={{
                color: "#ff6f00",
                display: { xs: "none", md: "flex" },
              }}
            >
              <img
                src={selectCountry.countryFlag}
                alt="English"
                width={"30px"}
                style={{ marginRight: "10px" }}
              />
              <Typography
                variant="span"
                sx={{ fontWeight: "600", letterSpacing: "2px" }}
              >
                {/* {selectCountry.name} */}
              </Typography>
            </Button>
            <Box>
              <Menu {...bindMenu(popupState)}>
                {data.map((item, index) => {
                  return (
                    <MenuItem key={index} onClick={popupState.close}>
                      <Button
                        onClick={() => {
                          SetselectCountry({
                            name: item.name,
                            countryFlag: item.flag,
                          });
                        }}
                        variant="contained"
                        sx={{
                          display: "flex",
                          justifyContent: "space-between",
                          alignItems: "center",
                          width: "100%",
                        }}
                      >
                        <img
                          src={item.flag}
                          alt={item.name}
                          width={"30px"}
                          style={{ marginRight: "6px" }}
                        />
                        <Typography
                          sx={{
                            fontWeight: 600,
                            letterSpacing: "1",
                          }}
                          variant="span"
                        >
                          {item.name}
                        </Typography>
                      </Button>
                    </MenuItem>
                  );
                })}
              </Menu>
            </Box>
          </Box>

          <Box sx={{ display: { xs: "block", md: "none" } }}>
            {data.map((item, index) => {
              return (
                <Box key={index} sx={{ marginBottom: "15px" }}>
                  <Button
                    onClick={() =>
                      SetselectCountry({
                        name: item.name,
                        countryFlag: item.flag,
                      })
                    }
                    variant="contained"
                    sx={{
                      width: "100%",
                      display: "flex",
                      justifyContent: "space-between",
                      alignItems: "center",
                    }}
                  >
                    <img
                      src={item.flag}
                      alt={item.name}
                      width={"50px"}
                      style={{ marginRight: "50px" }}
                    />
                    <Typography
                      variant="span"
                      sx={{
                        fontWeight: "600",
                        letterSpacing: "3px",
                        fontSize: "20px",
                      }}
                    >
                      {item.name}
                    </Typography>
                  </Button>
                </Box>
              );
            })}
          </Box>
        </React.Fragment>
      )}
    </PopupState>
  );
};

export default CountryControl;
